@import url("https://fonts.googleapis.com/css2?family=Khand:wght@300;400;500;600;700&family=Roboto:ital,wght@0,300;0,700;0,900;1,300;1,700&display=swap");
*,
*::before,
*::after {
  box-sizing: border-box; }

a {
  text-decoration: none;
  cursor: pointer; }

ul,
ol,
dd {
  list-style-type: none;
  margin: 0;
  padding: 0; }

:root {
  --content-max-width: 1400px; }

body > * {
  max-width: calc(100% - 30px);
  margin: 0 auto; }

.c1, .c2, .c3, .c4, .c5, .c6, .c7, .c8, .c9, .c10 {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  max-width: 100%; }
  .c1 > *, .c2 > *, .c3 > *, .c4 > *, .c5 > *, .c6 > *, .c7 > *, .c8 > *, .c9 > *, .c10 > * {
    flex-basis: 100%;
    flex: 1 1 auto; }

.c2 > * {
  flex-basis: 50%; }

.c3 > * {
  flex-basis: calc(100% / 3); }

.c4 > * {
  flex-basis: 25%; }

.c5 > * {
  flex-basis: 20%; }

.c6 > * {
  flex-basis: calc(100% / 6); }

.flex-100 {
  flex-basis: 100%; }

.flex-50 {
  flex-basis: 50%; }

.flex-h-center {
  justify-content: center; }

.flex-v-center {
  align-items: center; }

.center {
  text-align: center; }

.content-width {
  padding-left: calc((100% - var(--content-max-width)) / 2);
  padding-right: calc((100% - var(--content-max-width)) / 2); }

@media only screen and (min-width: 1024px) {
  body > * {
    max-width: 1400px; }
  .c1, .c2, .c3, .c4, .c5, .c6, .c7, .c8, .c9, .c10 {
    flex-flow: row; } }

body * {
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  font-weight: 300; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Khand", sans-serif;
  font-weight: 600;
  line-height: 1.2;
  margin: 5px 0; }

h1,
h2 {
  margin: 10px 0; }

h1 {
  font-size: 30px; }

h2 {
  font-size: 24px; }

h3 {
  font-size: 18px; }

h4 {
  font-size: 14px; }

h5 {
  font-size: 12px; }

h6 {
  font-size: 10px; }

p {
  margin: 5px 0; }

body,
html {
  padding: 0;
  margin: 0; }

nav.main {
  background: black;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 40px;
  padding: 0 15px;
  max-width: 100%; }
  nav.main a {
    color: white;
    padding: 0 5px; }
  nav.main .logo {
    display: block;
    align-items: center; }
    nav.main .logo img {
      height: 27px;
      transition: transform .3s; }
    nav.main .logo:hover img {
      transform: scale(1.05); }
  nav.main ul.menu {
    display: flex; }

@media screen and (max-width: 1024px) {
  .hide-mobile {
    display: none !important; }
  .hide-desktop {
    display: inherit; } }

@media screen and (min-width: 1025px) {
  .hide-mobile {
    display: inherit; }
  .hide-desktop {
    display: none !important; } }

.wrap {
  flex-wrap: wrap; }
